.shadowed-panel-outer-container {
  width: 100%;

  .shadowed-panel-container {
    margin: 10px;
    padding: 10px;
    background-color: #FFF;
    text-align: center;
    border-radius: 5px;

    -webkit-box-shadow: 0 30px 70px -9px rgba(50,50,50,0.2);
    -moz-box-shadow: 0 30px 70px -9px rgba(50,50,50,0.2);
    box-shadow: 0 30px 70px -9px rgba(50,50,50,.2);
  }
}
